<template>
    <v-container class="signup-shell " fluid>
        <v-row class="fill-height" align="center" justify="center">
            <v-col cols="12" md="8" lg="5">
                <v-card class="signup-card" elevation="0">
                    <div class="signup-header">
                        <p class="eyebrow">Join us</p>
                        <h2>회원가입</h2>
                        <p class="subtitle">채팅과 피아노 연주를 함께 즐겨보세요.</p>
                    </div>
                    <v-card-text>
                        <v-form @submit.prevent="memberCreate" class="signup-form">
                            <v-text-field 
                              label="이름"
                              v-model="name"
                              variant="outlined"
                              color="primary"
                              prepend-inner-icon="mdi-account"
                              required
                            />
                            <v-text-field 
                              label="Email"
                              v-model="email"
                              type="email"
                              variant="outlined"
                              color="primary"
                              prepend-inner-icon="mdi-email"
                              required
                            />
                            <v-text-field 
                              label="Password"
                              v-model="password"
                              type="password"
                              variant="outlined"
                              color="primary"
                              prepend-inner-icon="mdi-lock"
                              required
                            />
                            <v-btn type="submit" color="primary" class="signup-btn" size="large" prepend-icon="mdi-check">
                                등록
                            </v-btn>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
export default{
    data(){
        return{
            name: "",
            email: "",
            password: "",
        }
    },
    methods:{
        async memberCreate(){
            const data = {
                name: this.name,
                email: this.email,
                password: this.password,
                
            }
            await axios.post(`${process.env.VUE_APP_API_BASE_URL}/member/create`, data);
            this.$router.push("/");
        }
    }
}


</script>
