<template>
    <v-app-bar app elevation="0" class="header-bar">
      
                <v-btn variant="text" class="nav-btn brand-btn" :to="{path:'/'}">
                     <p class="text-h5 font-weight-bold">Web Piano</p><v-icon icon="mdi-piano" size="x-large"/>
                </v-btn>


      
                <v-btn variant="text" class="nav-btn" :to="{path:'/member/list'}">회원목록</v-btn>
                <v-btn variant="text" class="nav-btn" :to="{path:'/groupchatting/list'}">채팅방목록</v-btn>
      
      
                <v-spacer />
                <div class="auth-actions">
                    <v-btn
                      v-if="isLogin"
                      class="pill-btn"
                      :to="{path:'/my/chat/page'}"
                      prepend-icon="mdi-account-circle"
                    >
                      My Chat
                    </v-btn>
                    <v-btn
                      v-if="!isLogin"
                      class="pill-btn"
                      color="primary"
                      variant="flat"
                      :to="{path:'/member/create'}"
                    >
                      회원가입
                    </v-btn>
                    <v-btn
                      v-if="!isLogin"
                      class="pill-btn"
                      variant="tonal"
                      :to="{path:'/login'}"
                    >
                      로그인
                    </v-btn>


                    <v-btn  
                      v-if="isLogin"
                      class="pill-btn"
                      color="red-accent-2"
                      variant="tonal"
                      @click="doLogout"
                    >
                      로그아웃
                    </v-btn>
                </div>
    
    </v-app-bar>
</template>
<script>
export default{
    data(){
        return {
            isLogin: false,
        }
    },
    created(){
       const token = localStorage.getItem("token");
       if(token){
        this.isLogin = true;
       }
    },
    methods:{
        async doLogout(){
            localStorage.clear();
            await this.$router.push('/login');
            window.location.reload();
        }
    }
}
</script>
