// Utility to render a 88-key piano keyboard into a target element.
const getHz = (N = 0) => 440 * Math.pow(2, N / 12);

const notes = ['A', 'A#', 'B', 'C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#'];

const freqs = (start, end) => {
    let black = 0;
    let white = -2;

    return Array(end - start)
        .fill()
        .map((_, i) => {
            const key = (start + i) % 12;
            const note = notes[key < 0 ? 12 + key : key];
            const octave = Math.ceil(4 + (start + i) / 12);

            if (i === 0 && note === 'C') {
                black = -3;
            }

            if (note.includes('#')) {
                black += 3;
                if (['C#', 'F#'].includes(note)) {
                    black += 3;
                }
            } else {
                white += 3;
            }

            return {
                note,
                freq: getHz(start + i),
                octave: note === 'B' || note === 'A#' ? octave - 1 : octave,
                offset: note.includes('#') ? black : white,
            };
        });
};

const renderTemplate = (data) =>
    data
        .map(
            (item) => `
    <button type="button"
        tabindex="-1"
        aria-label="${item.note}${item.octave}" 
        data-note="${item.note}${item.octave}" 
        style="--gcs:${item.offset}">
    </button>`
        )
        .join('\n');

export const renderPiano = (target = 'kb88', start = -48, end = 40) => {
    const element = typeof target === 'string' ? document.getElementById(target) : target;
    if (!element) {
        console.warn('[pianoRender] target element not found:', target);
        return;
    }

    element.innerHTML = renderTemplate(freqs(start, end));
};

export default renderPiano;
