<template>
    <v-container class="member-shell">
        <v-row justify="center">
            <v-col cols="12" lg="10">
                <v-card class="member-card" elevation="0">
                    <div class="member-header">
                        <div>
                            <p class="eyebrow">Team Directory</p>
                            <h2>회원 목록</h2>
                            <p class="subtitle">함께 채팅할 멤버를 선택하세요.</p>
                        </div>
                        <v-chip color="primary" variant="flat" class="count-chip" size="small">
                            총 {{ memberList.length }}명
                        </v-chip>
                    </div>
                    <v-table class="member-table">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>이름</th>
                                <th>Email</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="member in memberList" :key="member.id">
                                <td>#{{ member.id }}</td>
                                <td>
                                    <div class="member-info">
           
                                        <div>
                                            <p class="member-name">{{ member.name }}</p>
                                    
                                        </div>
                                    </div>
                                </td>
                                <td>{{ member.email }}</td>
                                <td class="text-right">
                                    <v-btn
                                      class="pill-btn"
                                      color="primary"
                                      variant="flat"
                                      prepend-icon="mdi-message-processing"
                                      @click="startChat(member.id)"
                                    >
                                      채팅하기
                                    </v-btn>
                                </td>
                            </tr>
                            <tr v-if="!memberList.length">
                                <td colspan="4" class="empty-state">
                                    표시할 회원이 없습니다.
                                </td>
                            </tr>
                        </tbody>
                    </v-table>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import { logIfNotAuthError } from '@/utils/httpErrorHandler';

export default{
    data(){
        return {
            memberList: []
        }
    },
    async created(){
        await this.loadMembers();
    },
    methods:{
        async loadMembers(){
            try{
                const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/member/list`);
                this.memberList = response.data;
            }catch(error){
                logIfNotAuthError('회원 목록을 불러오지 못했습니다.', error);
            }
        },
        async startChat(otherMemberId){
            try{
                const response = await axios.post(`${process.env.VUE_APP_API_BASE_URL}/chat/room/private/create?otherMemberId=${otherMemberId}`);
                const roomId = response.data;
                this.$router.push(`/chatpage/${roomId}`);
            }catch(error){
                logIfNotAuthError('채팅방 생성에 실패했습니다.', error);
            }
        }
    }
}
</script>
