<template>
    <v-container class="room-shell">
        <v-row justify="center">
            <v-col cols="12" lg="10">
                <v-card class="room-card" elevation="0">
                    <div class="room-header">
                        <div>
                            <p class="eyebrow">Group Sessions</p>
                            <h2>채팅방 목록</h2>
                            <p class="subtitle">참여하거나 새 방을 만들어보세요.</p>
                        </div>
                        <v-btn class="pill-btn" color="primary" variant="flat" prepend-icon="mdi-plus" @click="showCreateRoomModal = true">
                            채팅방 생성
                        </v-btn>
                    </div>
                    <v-table class="room-table">
                        <thead>
                            <tr>
                                <th>방 번호</th>
                                <th>방 제목</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="chat in chatRoomList" :key="chat.roomId">
                                <td>#{{ chat.roomId }}</td>
                                <td>
                                    <div class="room-info">
                  
                                        <div>
                                            <p class="room-name">{{ chat.roomName }}</p>
                                    
                                        </div>
                                    </div>
                                </td>
                                <td class="text-right">
                                    <v-btn class="pill-btn" color="primary" variant="tonal" prepend-icon="mdi-login" @click="joinChatRoom(chat.roomId)">
                                        참여하기
                                    </v-btn>
                                </td>
                            </tr>
                            <tr v-if="!chatRoomList.length">
                                <td colspan="4" class="empty-state">생성된 방이 없습니다.</td>
                            </tr>
                        </tbody>
                    </v-table>
                </v-card>
            </v-col>
        </v-row>
        <v-dialog v-model="showCreateRoomModal" max-width="500px">
            <v-card>
                <v-card-title class="text-h6">
                    채팅방 생성
                </v-card-title>
                <v-card-text>
                    <v-text-field label="방제목" v-model="newRoomTitle"/>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="grey" @click="showCreateRoomModal = false">
                        취소
                    </v-btn>
                    <v-btn color="primary" @click="createChatRoom">
                        생성
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

    </v-container>
</template>

<script>
import axios from 'axios';
import { logIfNotAuthError } from '@/utils/httpErrorHandler';

export default{
    data(){
        return {
            chatRoomList: [],
            showCreateRoomModal: false,
            newRoomTitle: "",

        }
    },
    async created(){
        await this.loadChatRoom();
    },
    methods: {
        async joinChatRoom(roomId){
            try{
                await axios.post(`${process.env.VUE_APP_API_BASE_URL}/chat/room/group/${roomId}/join`);
                this.$router.push(`/chatpage/${roomId}`);
            }catch(error){
                logIfNotAuthError('채팅방 참여에 실패했습니다.', error);
            }
        },
        async createChatRoom(){
            try{
                await axios.post(`${process.env.VUE_APP_API_BASE_URL}/chat/room/group/create?roomName=${this.newRoomTitle}`, null);
                this.showCreateRoomModal = false;
                await this.loadChatRoom();
            }catch(error){
                logIfNotAuthError('채팅방 생성에 실패했습니다.', error);
            }
            
        },
        async loadChatRoom(){
            try{
                const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/chat/room/group/list`);
                this.chatRoomList = response.data;
            }catch(error){
                logIfNotAuthError('채팅방 목록을 불러오지 못했습니다.', error);
            }
        }
    },
}
</script>
