<template>
  <v-app id="inspire">

   <v-main class="bg-grey-lighten-5 chat-main" >

                         <!-- 채팅  -->
    <v-navigation-drawer app permanent class="chat-drawer" :width="360">
      <div class="chat-panel">

        <div class="chat-panel__messages">
          <transition-group name="chat-fade" tag="div" class="chat-panel__stream">
            <div
              v-for="(msg, index) in messages"
              :key="`${msg.senderEmail}-${index}`"
              class="chat-row"
            >
              <div class="chat-row__inner">
                <span class="chat-row__meta">
                  {{ msg.senderEmail === senderEmail ? '나:' : msg.senderEmail }}
                </span>
                <p class="chat-row__text">
                  {{' ' + msg.message }}
                </p>
              </div>
            </div>
          </transition-group>
        </div>

        <div class="chat-panel__input">
          <v-text-field
            v-model="newMessage"
            label="메시지 입력"
            variant="outlined"
            hide-details
            density="comfortable"
            color="deep-purple-accent-2"
            @keyup.enter="sendMessage"
          >
            <template #append-inner>
              <v-btn
                icon="mdi-send-variant-outline"
                variant="text"
                density="comfortable"
                color="deep-purple-accent-2"
                @click.stop="sendMessage"
              />
            </template>
          </v-text-field>
        </div>
      </div>
    </v-navigation-drawer>

    <div class="piano-control-region">
      <div class="control-option-buttons">
        <v-btn
          class="option-btn"
          size="small"
          variant="tonal"
          color="deep-purple-accent-2"
          :class="{'option-btn--active': activeControlOption === 'pedal'}"
          @click="toggleControlPanel('pedal')"
        >
          설정
        </v-btn>
       
      </div>

      <div class="piano-control-panel" :class="{'is-visible': showControlPanel}">
        <div class="control-card">
          <div class="control-card__top">
            <p class="control-label"> 옵션</p>
            <v-btn
              icon="mdi-close"
              variant="text"
              density="comfortable"
              @click="closeControlPanel"
            />
          </div>
          <div class="control-section">
            <p class="control-label">페달</p>
            <v-btn
              id="sustain-btn"
              class="control-btn"
              variant="outlined"
              color="deep-purple-accent-2"
