            >
              페달 OFF
            </v-btn>
          </div>
          <div class="control-section">
            <div class="control-header">
              <p class="control-label">Key</p>
              <span id="octave-display" class="control-value">0</span>
            </div>
            <input
              id="octave"
              class="control-slider"
              type="range"
              min="-24"
              max="24"
              step="1"
              value="0"
            />
          </div>
          <div class="control-section">
            <div class="control-header">
              <p class="control-label">사운드</p>
              <span id="volume-display" class="control-value">0 dB</span>
            </div>
            <input
              id="volume"
              class="control-slider"
              type="range"
              min="-30"
              max="6"
              step="1"
              value="0"
            />
          </div>
        </div>
      </div>
    </div>


    
      
                <!--   -->
    <v-container class="chat-piano-container">
        <div class="piano-wrapper">
          <div id="chat-piano" class="kb piano-responsive"></div>
        </div>
      </v-container>
   </v-main>
  </v-app>
</template>

<script>
import SockJS from 'sockjs-client';
import Stomp from 'webstomp-client';
import axios from 'axios';
import { logIfNotAuthError } from '@/utils/httpErrorHandler';




export default {

    data(){
        return {
            messages: [],
            newMessage: "",
            stompClient: null,
            token: "",
            senderEmail: null,
            roomId: null,
            roomName: "",
            showControlPanel: false,
            activeControlOption: null,
        }
    },
    watch: {
        messages: {
            deep: true,
            handler(){
                this.$nextTick(() => this.scrollToBottom());
            }
        }
    },
    async created(){
        this.senderEmail = localStorage.getItem("email");
        this.roomId = this.$route.params.roomId;
        await this.fetchRoomInfo();
        await this.loadChatHistory();
        this.connectWebsocket();
    },
    // 화면을 완전히 꺼버렸을때
    beforeUnmount() {
        this.disconnectWebSocket();
    },
    
     mounted() {
    
    
    },
    methods: {


        async fetchRoomInfo(){
            try{
                const { data } = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/chat/getRoomInfo/${this.roomId}`);
                this.roomName = data.roomName;
            }catch(error){
                logIfNotAuthError('채팅방 정보를 불러오지 못했습니다.', error);
            }
        },

        async loadChatHistory(){
            try{
                const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/chat/history/${this.roomId}`);
                this.messages = response.data;
                this.$nextTick(() => this.scrollToBottom());
            }catch(error){
                logIfNotAuthError('채팅 기록을 불러오지 못했습니다.', error);
            }
        },

        connectWebsocket(){
            if(this.stompClient && this.stompClient.connected) return;
            // sockjs는 websocket을 내장한 향상된 js 라이브러리. http엔드포인트 사용.
            const sockJs = new SockJS(`${process.env.VUE_APP_API_BASE_URL}/connect`)
            this.stompClient = Stomp.over(sockJs);
            this.token = localStorage.getItem("token");
            this.stompClient.connect({
                Authorization: `Bearer ${this.token}`
            },
                ()=>{
                    this.stompClient.subscribe(`/topic/${this.roomId}`, (message) => {
                        const parseMessage = JSON.parse(message.body);
                        if (parseMessage.type === "message") {
                            this.messages.push(parseMessage);
                            this.scrollToBottom();
                        }
                        else if (parseMessage.type === "piano") { 
                            if (parseMessage.onOff === "on") { 
                                this.playNoteOn(parseMessage.note)
                            }

                            if (parseMessage.onOff === "off") { 
                                this.playNoteOff(parseMessage.note)
                            }
                            

                            
                        }

                    },{Authorization: `Bearer ${this.token}`})
                }
            )
        },

        
        sendMessage(){
            if(this.newMessage.trim() === "")return;
            if(!this.stompClient || !this.stompClient.connected){
                console.warn("STOMP connection not ready. Message not sent.");
                return;
            }
            const message = {
                type: "message",
                senderEmail: this.senderEmail,
                message: this.newMessage
            }
            this.stompClient.send(`/publish/chat/${this.roomId}`, JSON.stringify(message));
            this.newMessage = ""
        },



        scrollToBottom(){
            this.$nextTick(()=>{
                const chatBox = this.$el.querySelector(".chat-panel__messages");
                chatBox.scrollTop = chatBox.scrollHeight;
            })
        },

        async disconnectWebSocket(){
            try{
                await axios.post(`${process.env.VUE_APP_API_BASE_URL}/chat/room/${this.roomId}/read`);
            }catch(error){
                logIfNotAuthError('메시지 읽음 처리에 실패했습니다.', error);
            }
            if(this.stompClient && this.stompClient.connected){
                this.stompClient.unsubscribe(`/topic/${this.roomId}`);
                this.stompClient.disconnect();
            }
        },

        getInitial(value){
            if(!value) return '?';
            return value.trim().charAt(0).toUpperCase();
        },
        toggleControlPanel(option){
            if(this.showControlPanel && this.activeControlOption === option){
                this.showControlPanel = false;
                this.activeControlOption = null;
                return;
            }
            this.activeControlOption = option;
            this.showControlPanel = true;
        },
        closeControlPanel(){
            this.showControlPanel = false;
            this.activeControlOption = null;
        }
 
    },
}
</script>

<script setup>
import renderPiano from '../piano/pianoRender';
import '../piano/piano.css';
import { getCurrentInstance, onMounted } from 'vue'
import { onBeforeRouteLeave } from 'vue-router';

import * as Tone from "tone";
import { WebMidi } from 'webmidi';

const { proxy } = getCurrentInstance();

const isTypingTarget = (target) => {
    if (!target) return false;
    const tag = target.tagName;
    return tag === 'INPUT' || tag === 'TEXTAREA' || target.isContentEditable;
};

onBeforeRouteLeave(() => {
    proxy?.disconnectWebSocket && proxy.disconnectWebSocket();
});


onMounted(() => {
    renderPiano('chat-piano');


// 브라우저 자동재생 정책 
let audioStarted = false;
document.addEventListener("keydown", async () => {
    if (!audioStarted) {
        await Tone.start();
        audioStarted = true;
    }
});

// 소리 설정
const sampler = new Tone.Sampler({
    urls: {
        A0: "A0.mp3",
        C1: "C1.mp3",
        "D#1": "Ds1.mp3",
        "F#1": "Fs1.mp3",
        A1: "A1.mp3",
        C2: "C2.mp3",
        "D#2": "Ds2.mp3",
        "F#2": "Fs2.mp3",
        A2: "A2.mp3",
        C3: "C3.mp3",
        "D#3": "Ds3.mp3",
        "F#3": "Fs3.mp3",
        A3: "A3.mp3",
        C4: "C4.mp3",
        "D#4": "Ds4.mp3",
        "F#4": "Fs4.mp3",
        A4: "A4.mp3",
        C5: "C5.mp3",
        "D#5": "Ds5.mp3",
        "F#5": "Fs5.mp3",
        A5: "A5.mp3",
        C6: "C6.mp3",
        "D#6": "Ds6.mp3",
        "F#6": "Fs6.mp3",
        A6: "A6.mp3",
        C7: "C7.mp3",
        "D#7": "Ds7.mp3",
        "F#7": "Fs7.mp3",
        A7: "A7.mp3",
        C8: "C8.mp3"
    },
    volume: 1,
    mute: false,
    release: 1,
    baseUrl: "https://tonejs.github.io/audio/salamander/",
}).toDestination();



const noteBtns = document.querySelectorAll("button[data-note]");

const synthesiaColor = "#a855f7";
const getSynthesiaColor = () => synthesiaColor;

const MIN_EFFECT_HEIGHT = 15;
const DEFAULT_MAX_EFFECT_HEIGHT = 420;

const EFFECT_GROWTH_RATE = 0.28;
const RISE_EXTRA_DISTANCE = 220;
const activeSynthesiaEffects = new WeakMap();
const getDynamicEffectHeight = () => {
    const viewportHeight = window.innerHeight || DEFAULT_MAX_EFFECT_HEIGHT;
    return Math.max(DEFAULT_MAX_EFFECT_HEIGHT, viewportHeight + 260);
};

function cleanupSynthesiaEffect(targetBtn) {
    const state = activeSynthesiaEffects.get(targetBtn);
    if (!state) return;
    if (state.raf) cancelAnimationFrame(state.raf);
    state.effect.remove();
    activeSynthesiaEffects.delete(targetBtn);
}

function triggerSynthesiaEffect(targetBtn) {
    if (!targetBtn) return;
    cleanupSynthesiaEffect(targetBtn);

    const effect = document.createElement("span");
    effect.className = "synthesia-effect";
    effect.style.setProperty("--effect-color", getSynthesiaColor());
    effect.style.setProperty("--effect-length", `${MIN_EFFECT_HEIGHT}px`);
    targetBtn.appendChild(effect);

    const state = {
        effect,
        start: performance.now(),
        raf: null,
        maxHeight: getDynamicEffectHeight()
    };

    const grow = () => {
        const elapsed = performance.now() - state.start;
        const nextHeight = Math.min(
            MIN_EFFECT_HEIGHT + elapsed * EFFECT_GROWTH_RATE,
            state.maxHeight || DEFAULT_MAX_EFFECT_HEIGHT
        );
        effect.style.setProperty("--effect-length", `${nextHeight}px`);
        state.raf = requestAnimationFrame(grow);
    };

    grow();
    activeSynthesiaEffects.set(targetBtn, state);
}

function releaseSynthesiaEffect(targetBtn) {
    const state = activeSynthesiaEffects.get(targetBtn);
    if (!state) return;
    if (state.raf) {
        cancelAnimationFrame(state.raf);
    }

    const { effect, start } = state;
    const currentHeight = parseFloat(
        effect.style.getPropertyValue("--effect-length") || MIN_EFFECT_HEIGHT
    ) || MIN_EFFECT_HEIGHT;

    effect.style.setProperty("--effect-length", `${currentHeight}px`);
    const elapsed = performance.now() - start;
    const baseDuration = Math.min(2.2, Math.max(0.6, elapsed / 600));
    const keyRect = targetBtn?.getBoundingClientRect?.();
    const distanceToViewportTop = Math.max(0, keyRect ? keyRect.top : window.innerHeight);
    const riseDistance = distanceToViewportTop + currentHeight + RISE_EXTRA_DISTANCE;
    const pxPerSecond = 320;
    const distanceDuration = Math.max(0.9, riseDistance / pxPerSecond);
    const riseDuration = Math.min(4.2, Math.max(baseDuration, distanceDuration));
    effect.style.setProperty("--effect-rise-duration", `${riseDuration}s`);
    effect.style.setProperty("--effect-rise-distance", `${riseDistance}px`);
    effect.classList.add("synthesia-effect--rise");
    effect.addEventListener("animationend", () => effect.remove(), { once: true });
    activeSynthesiaEffects.delete(targetBtn);
}

// 옥타브 변경
let transepos = 0;
const octaveEl = document.getElementById("octave");
const octaveValueEl = document.getElementById("octave-display");
const updateOctaveDisplay = () => {
    if (octaveValueEl) {
        const prefix = transepos > 0 ? "+" : "";
        octaveValueEl.textContent = `${prefix}${transepos}`;
    }
};
const syncOctaveInput = () => {
    if (octaveEl) {
        octaveEl.value = transepos;
    }
    updateOctaveDisplay();
};

if (octaveEl) {
    const initialValue = Number(octaveEl.value);
    if (!Number.isNaN(initialValue)) {
        transepos = initialValue;
    }
    updateOctaveDisplay();
    octaveEl.addEventListener("input", () => {
        const nextValue = Number(octaveEl.value);
        if (!Number.isNaN(nextValue)) {
            transepos = nextValue;
        }
        updateOctaveDisplay();
    });
} else {
    console.warn("[tone] #octave element not found. Transpose controls disabled.");
}


const transePosKeyMap = {
    ArrowLeft: -1,
    ArrowRight: 1,
    ArrowUp: 12,
    ArrowDown: -12,
    CapsLock: -12,
    Alt: 24

};

document.addEventListener("keydown", (e) => {
    if (e.repeat) return;
    if (transePosKeyMap[e.key]) {
        if (e.key.includes("Arrow")) {
            e.preventDefault();
        }
        transepos += transePosKeyMap[e.key];
        syncOctaveInput();
    }
});

document.addEventListener("keyup", (e) => {

    if (e.key == "CapsLock") {
        transepos -= transePosKeyMap[e.key];
        syncOctaveInput();
    }

    if (e.key == "Alt") {
        transepos -= transePosKeyMap[e.key];
        syncOctaveInput();
    }
    if (transePosKeyMap[e.key] && e.key.includes("Arrow")) {
        e.preventDefault();
    }


});






    function sendNote(note , onOff) { 
        if(!proxy?.stompClient || !proxy.stompClient.connected){
            console.warn("STOMP connection not ready. Piano event not sent.");
            return;
        }
        const message = {
            type: "piano",
            note: note,
            onOff: onOff
        }

        proxy.stompClient.send(`/publish/piano/${proxy?.roomId}`, JSON.stringify(message));

    }

    const activeNotesMap = new Map();

    


function playNoteOn(note) {
    console.log("noteOn = " + note)
    const Tnote = transpose(note)
    sampler.triggerAttack(Tnote)
    pressNoteButton(Tnote, "on")
    activeNotesMap.set(note, Tnote);
}

function playNoteOff(note) {
     console.log("noteoff = " + note)

    const Tnote = activeNotesMap.get(note);
    if (!Tnote) return;

    if (!sustain) {
        sampler.triggerRelease(Tnote);
    }
    pressNoteButton(Tnote, "off");
    activeNotesMap.delete(note);
}


function noteOn(note) {
        sendNote(note , "on");
       
}

function noteOff(note) {
        sendNote(note ,"off");
       
}


    proxy.playNoteOn = playNoteOn;
    proxy.playNoteOff = playNoteOff;


let sustain = false;




function transpose(note) {

    return Tone.Frequency(note).transpose(transepos).toNote()
}


function pressNoteButton(note, onOff) {
    const noteBtn = document.querySelector(`button[data-note="${note}"]`);
    if (!noteBtn) return;
    if (onOff == "on") {
        noteBtn.classList.add("keydown");
        triggerSynthesiaEffect(noteBtn);
    } else {
        noteBtn.classList.remove("keydown");
        releaseSynthesiaEffect(noteBtn);
    }


}





//클릭
if (noteBtns.length === 0) {
    console.warn("[tone] piano keys not found. Pointer controls disabled.");
} else {
    noteBtns.forEach(noteBtn => {

        noteBtn.addEventListener('pointerdown', () => {
            noteOn(noteBtn.getAttribute("data-note"));
        });

        noteBtn.addEventListener('pointerup', () => {
            noteOff(noteBtn.getAttribute("data-note"));
        });

        noteBtn.addEventListener('pointerleave', () => {
            noteOff(noteBtn.getAttribute("data-note"));
        });
    });
}


// 키보드
const keyLayout = {
    MPP: {
        49: 'G#3', // 1
        50: 'A#3', // 2
        52: 'C#4', // 4
        53: 'D#4', // 5
        55: 'F#4', // 7
        56: 'G#4', // 8
        57: 'A#4', // 9
        45: 'C#5', // -
        61: 'D#5', // =

        113: 'A3', // q
        119: 'B3', // w
        101: 'C4', // e
        114: 'D4', // r
        116: 'E4', // t
        121: 'F4', // y
        117: 'G4', // u
        105: 'A4', // i
        111: 'B4', // o
        112: 'C5', // p
        91: 'D5', // [
        93: 'E5', // ]

        97: 'G#2', // a
        115: 'A#2', // s
        102: 'C#3', // f
        103: 'D#3', // g
        106: 'F#3', // j
        107: 'G#3', // k
        108: 'A#3', // l
        39: 'C#4', // '

        122: 'A2', // z
        120: 'B2', // x
        99: 'C3', // c
        118: 'D3', // v
        98: 'E3', // b
        110: 'F3', // n
        109: 'G3', // m
        44: 'A3', // ,
        46: 'B3', // .
        47: 'C4'  // /
    }
};

document.addEventListener("keydown", (k) => {
    if (isTypingTarget(k.target)) return;
    if (k.repeat) return; // 반복 호출 방지
    if (!keyLayout.MPP[k.key.charCodeAt(0)]) return;

    //키보드 입력과 같은 노트 매핑
    const note = keyLayout.MPP[k.key.charCodeAt(0)];

    noteOn(note);
});

document.addEventListener("keyup", (k) => {
    if (isTypingTarget(k.target)) return;
    if (!keyLayout.MPP[k.key.charCodeAt(0)]) return;

    const note = keyLayout.MPP[k.key.charCodeAt(0)];

    noteOff(note);
});


//페달
const SustainBtn = document.getElementById("sustain-btn");

const setSustainLabel = () => {
    if (!SustainBtn) return;
    SustainBtn.innerText = sustain ? "페달 ON" : "페달 OFF";
};

function toggleSustain() {
    sustain = !sustain;
    setSustainLabel();
}

if (SustainBtn) {
    setSustainLabel();
    SustainBtn.addEventListener("click", toggleSustain);
} else {
    console.warn("[tone] #sustain-btn element not found. Sustain button disabled.");
}
document.addEventListener("keydown", (e) => {

    if (e.key === "Backspace") {
        toggleSustain();
    }
    if (e.key === " ") {
        sustain = true
        setSustainLabel();
    }
});

document.addEventListener("keyup", (e) => {
    if (e.key === " ") {
        sustain = false
        setSustainLabel();
    }
});




// 볼륨 조절
const volumeEl = document.getElementById("volume");
const volumeDisplayEl = document.getElementById("volume-display");
const updateVolumeDisplay = (value) => {
    if (volumeDisplayEl) {
        volumeDisplayEl.textContent = `${value} dB`;
    }
};

if (volumeEl) {
    const initialVolume = Number(volumeEl.value);
    const normalizedVolume = Number.isNaN(initialVolume) ? 0 : initialVolume;
    sampler.volume.value = normalizedVolume;
    updateVolumeDisplay(normalizedVolume);
    volumeEl.addEventListener("input", () => {
        const nextVolume = Number(volumeEl.value);
        if (!Number.isNaN(nextVolume)) {
            sampler.volume.value = nextVolume;
            updateVolumeDisplay(nextVolume);
        }
    });
} else {
    console.warn("[tone] #volume element not found. Volume slider disabled.");
}






WebMidi
    .enable()
    .then(onEnabled)
    .catch(err => {
        console.error("미디 접근을 허용해주세요:", err);
    });

function onEnabled() {

    console.log("미디 접근 허용")
    if (WebMidi.inputs.length < 1) {
        console.log("MIDI장치를 연결해주세요")
    } else {

        WebMidi.inputs.forEach((device) => {
            console.log("===장치 정보===")
            console.log(device.id)
            console.log(device.manufacturer)
            console.log(device.name)
            console.log("===============")
        });
    }



    const mySynth = WebMidi.inputs[0];

    if (!mySynth) {
        console.log("MIDI 장치를 찾을 수 없습니다.");
        return;
    }

    // 채널 = 미디 장치 수
    mySynth.channels[1].addListener("noteon", e => {
        const note = e.note.identifier
        noteOn(note);

    });

    // noteoff 이벤트도 처리
    mySynth.channels[1].addListener("noteoff", e => {
        const note = e.note.identifier
        noteOff(note);

    });
}



})




</script>

<style>
