    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    padding-bottom: 48px;
    min-height: 100vh;
    background: radial-gradient(circle at top, #131b2d 0%, #05070f 75%);
    color: #e5e8ff;
}

