    position: absolute;
    top: 18px;
    right: 18px;
    z-index: 6;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
}

.control-option-buttons{
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.option-btn{
    text-transform: none;
    letter-spacing: 0.05em;
}

.option-btn--active{
    box-shadow: 0 8px 18px rgba(126, 118, 255, 0.35);
}

.piano-control-panel{
    opacity: 0;
    pointer-events: none;
    transform: translateY(-8px);
    transition: opacity 0.2s ease, transform 0.2s ease;
}

.piano-control-panel.is-visible{
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
}

.control-card{
    width: 280px;
    padding: 16px;
    border-radius: 18px;
