<template>
  <v-app id="inspire">
    <HeaderComponent />
    <v-main class="bg-grey-lighten-4">
      <router-view />
    </v-main>
    <v-dialog
      v-model="showLoginDialog"
      width="420"
      persistent
    >
      <v-card>
        <v-card-title class="text-h5 font-weight-bold">
          로그인 후 이용해주세요
        </v-card-title>
        <v-card-text>
          회원 목록과 채팅 기능은 로그인한 사용자만 이용할 수 있습니다. 로그인 후 다시 시도해주세요.
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn variant="text" @click="closeLoginDialog">
            닫기
          </v-btn>
          <v-btn color="primary" variant="flat" @click="goToLogin">
            로그인 하러가기
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script setup>
import { onBeforeUnmount, onMounted, ref } from 'vue';
import { useRouter } from 'vue-router';
import HeaderComponent from './components/HeaderComponent.vue'
import { LOGIN_REQUIRED_EVENT } from './utils/authEvents';

const router = useRouter();
const showLoginDialog = ref(false);

const closeLoginDialog = () => {
  showLoginDialog.value = false;
};

const goToLogin = () => {
  closeLoginDialog();
  router.push('/login');
};

const handleLoginRequired = () => {
  showLoginDialog.value = true;
};

onMounted(() => {
  window.addEventListener(LOGIN_REQUIRED_EVENT, handleLoginRequired);
});

onBeforeUnmount(() => {
  window.removeEventListener(LOGIN_REQUIRED_EVENT, handleLoginRequired);
});
</script>

<style>
html, body, #app {
  height: 100%;
  margin: 0;
}
body {
  overflow: hidden;
}
.v-application__wrap {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

</style>
