<template>
    <v-container class="login-shell" fluid>
        <v-row class="fill-height" align="center" justify="center">
            <v-col cols="12" md="8" lg="5">
                <v-card class="login-card" elevation="0">
                    <div class="login-header">
                        <p class="eyebrow">Welcome Back</p>
                        <h2>로그인</h2>
                        <p class="subtitle">나의 채팅 공간으로 돌아와주세요.</p>
                    </div>
                    <v-card-text>
                        <v-form @submit.prevent="doLogin" class="login-form">
                            <v-text-field 
                                label="Email"
                                v-model="email"
                                type="email"
                                variant="outlined"
                                color="primary"
                                prepend-inner-icon="mdi-email"
                                required
                            />
                            <v-text-field 
                                label="Password"
                                v-model="password"
                                type="password"
                                variant="outlined"
                                color="primary"
                                prepend-inner-icon="mdi-lock"
                                required
                            />
                            <v-btn type="submit" color="primary" class="login-btn" size="large" prepend-icon="mdi-login">
                                로그인
                            </v-btn>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import { jwtDecode } from 'jwt-decode';
export default{
    data(){
        return{
            email: "",
            password: "",
        }
    },
    methods:{
        async doLogin(){
            const loginData = {email:this.email, password:this.password};
            const response = await axios.post(`${process.env.VUE_APP_API_BASE_URL}/member/doLogin`, loginData);
            const token = response.data.token;
            const role = jwtDecode(token).role;
            const email = jwtDecode(token).sub;
            localStorage.setItem("token", token);
            localStorage.setItem("role", role);
            localStorage.setItem("email", email);
            window.location.href="/";
        }
    }
}


</script>
